.about__main {
    position: relative;
}

.icons__trip {
    display: flex;
    justify-content: start;
    align-items: center;
}

.i__con {
    width: 70px;
    height: 70px;
    background-color: #e9e9e9e1;
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.trip_icon {
    font-size: 25px;
}

.light__text__cont {
    position: relative;
    margin-top: 60px;
}

.light__text__cont h2 {
    font-size: 35px;
    font-weight: 600;
    margin-bottom: 30px;
}

.light_text {
    position: absolute;
    font-size: 70px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.1);
    top: -25px;
    left: 0px;
}

.book__now__button button {
    border: 1px solid black;
    padding: 10px 40px;
    border-radius: 100px;
    color: white;
    background-color: rgb(19, 36, 63);
    font-size: 18px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
}

.book__now__button button:hover {
    background-color: rgb(30, 56, 97);
}

.about__desc {
    font-weight: 200;
    font-size: 14px;
    font-family: 'Montserrat', sans-serif;
}

.contact__form button {
    border: 1px solid black;
    padding: 10px 40px;
    border-radius: 100px;
    color: white;
    background-color: rgb(19, 36, 63);
    font-size: 18px;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
}

.content {
    display: none;
}

.show__content {
    display: block;
}


.about__tab__buttons button {
    padding: 8px 50px;
    border-radius: 4px;
    background-color: rgb(196, 196, 196);
    color: #333333;
    border: 0;
}

.about__tab__buttons button:hover {
    background-color: #333333;
    color: #fff;
}

.about__tab__buttons button.active {
    background-color: #333333;
    color: #fff;
}

@media screen and (max-width:478px) {
    .trip_icon {
        background-color: #e9e9e9e1;
        padding: 14px;
        border-radius: 50%;
        font-size: 25px;
    }

    .icons__trip p {
        font-size: 15px;
    }

    .light_text {
        font-size: 59px;
        top: -10px;
        left: 0px;
    }

    .light__text__cont span {
        font-size: 55px;
    }

    .about__tab__buttons button {
        padding: 8px 16px;
        font-size: 13px;
    }

}