.circle {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(197, 197, 197, 0.822);
    border-radius: 100%;
    height: 75px;
    width: 75px;
    text-transform: uppercase;
    font-weight: 600;
}

.circle:hover {
    background-color: #333333;
    color: white;
}

