* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none !important;
}

.Mon_font {
    font-family: 'Montserrat', sans-serif;
}

.hero__heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 3.5vw;
    font-weight: 600;
    margin-bottom: 2vh;
}

.sub__heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 2vw;
    font-weight: 300;
}

.category__heading {
    font-family: 'Montserrat', sans-serif;
    font-size: 2.5vw;
    font-weight: 600;
    margin-bottom: 10vh;
}

.content_text_grey {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.5vw;
    font-weight: 500;
    color: grey;

}

.uppercase {
    text-transform: uppercase;
}

@media screen and (max-width:478px) {
    .hero__heading {
        font-size: 8vw;
    }

    .sub__heading {
        font-size: 4vw;
    }

    .category__heading {
        font-size: 5vw;
    }

    .content_text_grey {
        font-size: 3.2vw;
    }
}