.team__box {
    /* display: flex; */
}

.team__box img {
    box-shadow: 0 3px 8px rgba(0, 0, 0, 0.4);
    border-radius: 100%;
    background: transparent;
}

.about__heading {
    color: #474747;
    font-size: 28px;
}

.team__box h4 {
    color: #474747 !important;
    font-weight: 600;
}

.team__box p {
    font-weight: 100;
}

.team__box i {
    font-size: 25px;
    margin: 10px;
}

/* OUR WARRIOS  */
.our__warriors p {
    color: rgba(0, 0, 0, 0.7);
    font-size: 14px;
    font-weight: 100;
}

@media screen and (max-width:478px) {
    .about__heading {
        color: #474747;
        font-size: 18px;
        font-weight: 700;
    }
}