.content {
    display: none;
}

.show {
    display: block;
}

.book__cont {
    border: 3px solid rgba(0, 0, 0, 0.8);
    border-radius: 15px;
    overflow: hidden;
    min-height: 300px;
}

.border__date {
    border: 1px solid rgb(146, 146, 146);
    padding: 15px;
    border-radius: 10px;
}

.date__cont input,
.radio {
    color: red !important;
    background-color: black;
    font-size: 50px !important;
}

@media screen and (max-width:478px) {
    .booking__page h1 {
        font-size: 20px !important;
    }

    .book_th {
        font-size: 12px;
    }

    .fa-add {
        font-size: 16px;
    }

    .fa-minus {
        font-size: 16px;
    }
}