.main_landing {
    overflow: hidden;
    width: 100%;
    position: relative;
}

.main_bg_cover {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.7) 0%, rgba(0, 0, 0, 0.7) 100%), url(https://ik.imagekit.io/workcations/gallery/triplist/main-cover.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    overflow: hidden;
    z-index: 1;
    padding-top: 70px;
}

.hero__section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 50px;
}

.search__field {
    width: 45vw;
    height: 4vw;
    border: 1px solid rgb(10, 158, 136);
    border-radius: 5px;
    padding: 8px;
    font-size: 1.5vw;
    font-weight: 400;
    color: white;
    background: rgba(255, 255, 255, 0.2);
    position: relative;
}

.search__location {
    position: relative;
    margin-top: 8vh;
    margin-bottom: 10vh;
}

.search__location i {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 5px 5px 0;
    padding: 19px;
    background-color: rgb(10, 158, 136);
    color: #fff;
}

.site__review {
    display: flex;
    width: 100%;
    justify-content: space-evenly;
    margin-top: 50px;
}

.img__review {
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}

.img__review img {
    width: 50px;
}

.img__review span {
    font-size: 20px;
    font-weight: 300;
    font-family: 'Montserrat', sans-serif;
}

.exlpore__container {
    min-height: 30vh;
}

.trips__category {
    padding: 30px;
    margin-bottom: 30px;
}

.trips__category .category__heading {
    color: rgb(10, 158, 136);
}

.category__container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 100px;
}

.category__feature {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
}

.category__feature img {
    width: 70px;
}

.category__feature span {
    color: #3a3a3a;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600;
}

/* ================================================ UPCOMING TRIPS CSS */
.upcoming__trips .category__heading {
    color: #333333;
}

.upcoming__trips select {
    background-color: transparent;
    padding: 5px 8px;
    border: 2px solid rgba(87, 87, 87, 0.2);
    border-radius: 5px;
    margin: 0 20px;
    font-size: 18px;
}

.trips__container {
    margin-top: 15px;
    padding: 0 30px;
}

.trips {
    width: 300px;
    padding: 10px;
}

.trips p {
    margin-top: 10px;
    margin-bottom: 0;
    font-size: 14px;
    font-weight: 600;
    font-family: 'Montserrat', sans-serif;
    color: rgb(10, 158, 136);
}

.trips img {
    width: 300px;
    width: 21vw;
    height: 17vw;
    border-radius: 3vw;
}

.trips h5 {
    font-family: 'Montserrat', sans-serif;
    font-size: 18px;
    color: #333333;
}

/* ================================================ TALK TO EXPERT */
.talk__to__bg {
    /* background: url(""); */
    background-color: rgb(19, 36, 63);
    padding: 60px 150px;
}

.talk__to__expert {
    padding: 40px 30px;
    border-radius: 20px;
}

.talk__to__expert h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.8vw;
    color: rgb(19, 36, 63);
}

.talk__to__expert button {
    color: #fff;
    background-color: rgb(19, 36, 63);
    border: 1px solid rgba(255, 255, 255, 0.2);
    border-radius: 15px;
    padding: 10px 20px;
    text-transform: uppercase;
    font-size: 18px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
    margin-top: 10px;

}

/* ================================================ LOCATIONS LIST */

.locations__lists img {
    border-radius: 15px;
    box-shadow: 0 3px 8px rgba(00, 0, 0, 0.2);
}

.trip__bg {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.trip__bg button {
    position: absolute;
    z-index: 15;
    top: 50%;
    right: 25%;
    padding: 10px 50px;
    background-color: #1f1f1fd0;
    border: 3px solid rgb(10, 158, 136) !important;
    color: rgb(10, 158, 136);
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
}

.testimonials__cont {
    margin-bottom: 80px;
    padding: 10px 200px;
}

.testimonials img {
    width: 600px;
    border-radius: 15px;
}

/* ================================================ FOOTER */
.footer {
    background-color: rgb(19, 36, 63);
    padding: 60px 50px 10px 50px;
}

.page__links {
    display: flex;
    justify-content: center;
}

.page__links a {
    color: white;
    margin: 0 20px;
    font-size: 20px;
    font-family: 'Montserrat', sans-serif;
}

.trip__locations {
    margin-top: 20px;
    justify-content: center;
}

.trip__locations a {
    color: white;
    margin: 0 20px;
    font-weight: 300;
    font-family: 'Montserrat', sans-serif;
}

.social__media__links i {
    color: #fff;
    font-size: 20px;
    margin: 0 15px;
}



/* ================================================ MOBILE SCREEEN CSS */
@media screen and (max-width:478px) {
    .main_bg_cover {
        background-position: center;
        padding: 0 30px;
        height: 95vh;
    }

    .hero__section {
        height: 80vh;
        margin-top: 150px;
    }

    .other__section {
        height: 50vh;
        margin-top: 15vh;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        text-align: center;
        /* border: 2px solid red; */
    }

    .search__field {
        width: 80vw;
        height: 10vw;
        font-size: 3vw;
        font-weight: 400;
        margin-top: 0;
    }

    .search__location i {
        padding: 12px;
    }

    .search__location input {
        color: #fff !important;
    }

    .site__review {}

    .img__review {
        padding: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }

    .img__review img {
        width: 30px;
    }

    .img__review span {
        font-size: 10px;
    }

    .category__container {
        padding: 0 20px;
    }

    .category__feature img {
        width: 50px;
    }

    .category__feature span {
        font-size: 10px;
        font-weight: 400;
    }

    /* ================================================ TALK TO EXPERT */
    .talk__to__expert h1 {
        font-size: 4vw;
    }

    .talk__to__expert button {
        font-size: 3vw;
        padding: 10px 20px;
        display: block;
        margin: 20px auto 5px auto;
    }

    .talk__to__expert p {
        font-size: 3.5vw;
        margin: 0;
        padding: 0;
        text-align: center;
    }


    /* ================================================ TRIPS SECTION */
    .trips__container {
        overflow: hidden;
    }

    .trips {
        width: 100%;
        padding: 0 0 0 10px;
    }

    .trips p {
        font-size: 14px;
        margin-top: 5px;
        margin-bottom: 0;
    }

    .trips img {
        width: 56vw;
        height: 56vw;
        border-radius: 50px;
    }

    .trips h5 {
        font-size: 14px;
        font-weight: 600;
    }

    .talk__to__bg {
        /* background: url(""); */
        background-color: rgb(19, 36, 63);
        padding: 30px 20px;
    }

    .page__links a {
        font-size: 10px;
        font-family: 'Montserrat', sans-serif;
    }

    .trip__locations a {
        color: white;
        margin: 0 20px;
        font-weight: 300;
        font-family: 'Montserrat', sans-serif;
    }

    /* =========================== Testimonials  */
    .testimonials__cont {
        margin-bottom: 80px;
        padding: 10px 40px;
    }

    .testimonials img {
        width: 100%;
    }

    /* .arrow {
        display: none;
    } */
}